}

.App {
  width: 1000px;
  min-height: 100vh;
  background-color: #F7F9FB;
  color: #2E364F;
  margin-left: 250px;
  margin-right: 250px; }

.title-header {
  display: inline-block;
  height: 50px;
  line-height: 50px;
  font-family: inherit;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
  margin-left: 30px;
  padding-left: 30px;
  padding-right: 50px;
  border-left: 2px solid #EF5939; }
  .title-header:after {
    content: "";
    display: block;
    clear: both; }

.Notice {
  width: 100%;
  clear: both;
  margin-top: 100px;
  padding-top: 20px;
  padding-bottom: 50px;
  font-family: "Noto Sans KR", sans-serif;
  font-size: 1.2rem; }
  .Notice form {
    margin-top: 30px;
    margin-bottom: 30px;
    padding-left: 30px;
    padding-right: 30px; }
    .Notice form button {
      background-color: #2D5D7C;
      color: #fff;
      border: 1px solid transparent;
      margin-top: 30px; }

