.Header {
  width: 100%;
  height: 100px;
  background-color: #2D5D7C;
  font-family: "Noto Sans KR", sans-serif; }
  .Header .title {
    font-size: 2.2rem;
    font-weight: bold;
    font-family: inherit;
    line-height: 100px;
    color: #F3F0E2;
    margin: 0;
    padding-left: 40px; }
  .Header .menu {
    width: 100%;
    height: 70px;
    background-color: #2E364F; }
    .Header .menu .item {
      font-size: 1.2rem;
      font-weight: bold;
      font-family: inherit;
      color: #F3F0E2;
      line-height: 70px; }
      .Header .menu .item:first-child {
        margin-left: 40px; }
      .Header .menu .item:after {
        content: "";
        margin-left: 30px; }
    .Header .menu .active {
      color: #EF5939; }
    .Header .menu:after {
      content: "";
      display: block;
      clear: both; }

